@import "main.css";
@import "navbar.css";
@import "projects.css";
@import "experience.css";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Poppins:wght@200;400;500;600;700&display=swap');

/* scrollbar */
/* Hide scrollbar for Chrome, Safari and Opera */
::-webkit-scrollbar {
  display: none;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}

* {
  cursor: url(../assets/cursor.png), auto;
}

body {
  font-family: 'Roboto Mono', monospace;
  background: #ffeca4;
  overflow: scroll;
  color: #5d270c;
  text-shadow: .5px .5px 1.5px #6b6749;
}

/* Pointer cursor for clickable links */
a {
  cursor: url(../assets/pointer_cursor.png), pointer;
}

img {
  cursor: url(../assets/pointer_cursor.png), pointer;
}

.content {
  margin-top: 10em;
}

.left {
  float: left;
  width: 42%;
}

.right {
  margin-top: 20px;
  float: right;
  width: 58%;
}

/*Mobile*/
@media (max-width: 480px) {
  .content {
    margin-top: 25px;
  }

  .left,
  .right {
    width: 100%;
    float: none;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}